<template>
  <div>
    <div id="splash-screen">
      <img src="./assets/bazovyy-garderob-ne-prigodilsya.jpg" alt="First slide">
    </div>
    <aside>123</aside>
    <section class="container-fluid">
      <t-shirts :tshirts="tshirts"
                @nextPage="loadTshirts"
                :tshirtsLoaded="tshirtsLoaded">
      </t-shirts>
    </section>
  </div>
</template>

<script>
import axios from 'axios'

const Tshirts = (resolve) => {
  require(['./components/Tshirts/Tshirts.vue'], resolve);
};

export default {
  name: 'app-catalog',
  created() {
    let keywords = document.querySelector("meta[name=keywords]");
    let description = document.querySelector("meta[name=description]");

    document.title = this.translate('meta.Catalog.title');
    keywords.content = this.translate('meta.Catalog.keywords');
    description.content = this.translate('meta.Catalog.description');
  },
  created() {
    this.loadTshirts(1);
  },
  components: {
    't-shirts': Tshirts,
  },
  methods: {
    loadTshirts(page) {
      console.log(page);
      if(this.isFirstLoadTShirts() || this.isNextPage(page)) {
        axios.get('/api/v1.0/tshirts', {
            params: {
              page: page,
              lang: this.language,
            }
          })
            .then((response) => {
              if(this.tshirts.tshirtList == undefined) {
                this.tshirts = response.data;
              } else {
                this.tshirts.tshirtList = 
                    this.tshirts.tshirtList.concat(response.data.tshirtList);
                this.tshirts.paginate = response.data.paginate;
                this.tshirts.lang = response.data.lang;
                this.tshirts.config = response.data.config;
              }
              console.log(this.tshirts, response);
            })
            .catch((error) => {
              this.$snotify.error(this.translate('info.something_went_wrong'));
              console.log(error);
            });
      }
    },
    isFirstLoadTShirts() {
      return this.tshirts.tshirtList == undefined;
    },
    isNextPage(currentPage) {
      try {
        let isNextPage = currentPage <= this.tshirts.paginate.last_page;
        if(!isNextPage) {
          this.tshirtsLoaded = true;
        }
        return isNextPage;
      } catch(e) {
        return false;
      }
    },
  },
  data() {
    return {
      tshirtsLoaded: false,
      tshirts: {},
    }
  },
}
</script>

<style lang="scss">

</style>



